.chat {
  position: relative;
  height: 100%;
  overflow: hidden;
  display: flex;
  flex-direction: column;

  flex:4;
}



.inputbox {
  background-color: rgb(128, 128, 128);
  display: flex;
  height: 50px;
}

.inputbox .label1 {
  font-size: 1.5em;
  text-align: right;
}

.inputbox .text {
  font-size: 1em;
  padding-left: 8px;
  padding-right: 8px;
  flex: 1;
}

.inputbox .button {
  background-color: rgb(255, 255, 255);
  font-size: 1em;
  min-width: 40px;
  transition: background-color 0.4s;
}

.inputbox .button:hover {
  background-color: #929090;
  transition: background-color 0.4s;
}